.container{
    background-color: #FFCD9E;
    display: flex;
    height: 450px;
}

.left{
    margin-top: 50px;
    margin-left:130px ;

}

.header{
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    color: #282828;
    width: 70%;
}

.para1{
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    color: #484848;
    width: 60%;
    line-height: 120%;
}

.tag{
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    color: #282828;
}
img{
    height: 400px;

}
input{
    height: 32px;
    background: #FFFFFF;
    border-radius: 8px;
    padding-left: 20px;
    width: 15rem;
    margin-top: 12px;
    margin-right: 12px;
    border: 3px solid #000000;
}



button{
    background: #282828;
    border: none;
    border-radius: 8px;
    color: white;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    width: 10em;
    height: 40px;
    cursor: pointer;
}

.bottom-line{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    color: #FFFFFF;
    margin-top: 12px;
    margin-left: 20px;
}
